/* SWITCH STYLING */
.toggler .switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.toggler .switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.toggler .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
}

.toggler .slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
}

.toggler input:checked + .slider {
  background-color: #962d2d;
}

.toggler input:focus + .slider {
  box-shadow: 0 0 1px #962d2d;
}

.toggler input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.toggler .slider.round {
  border-radius: 34px;
}

.toggler .slider.round:before {
  border-radius: 50%;
}

