.loader_parent {
  display: grid;
  place-items: center;
  height: 100vh;
  overflow: hidden;
}

.loader {
  display: grid;
  place-items: center;
  height: 100vh;
  width: 100%;
  /* border: 16px solid #f3f3f3; */
  border: 16px solid #212121;
  /* Light grey */
  /* border-top: 16px solid #048023; */
  border-top: 12px solid #962d2d;
  /* Dark Green */
  border-radius: 50%;
  width: 150px;
  height: 150px;
  -webkit-animation: spinloader 2s linear infinite;
          animation: spinloader 2s linear infinite;
}

.loader img {
  height: 60px;
  width: 60px;
  -webkit-animation: spinlogo 2s linear infinite;
          animation: spinlogo 2s linear infinite;
}

@-webkit-keyframes spinloader {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spinloader {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes spinlogo {
  0% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }

  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@keyframes spinlogo {
  0% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }

  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@media screen and (max-width: 500px) {
  .loader {
    width: 115px;
    height: 115px;
    border: 11px solid #212121;

    border-top: 8px solid #962d2d;
  }
}

