  border: 1px solid #fff;
  outline: none;
  background: transparent;
  transition: 0.3s linear;
  cursor: pointer;
  color: #fff;
  font-size: 1.05em;
}

.btn:hover {
  background-color: #962d2d;
  color: white;
}

.input:focus,
.msg .area:focus {
  border: 2px solid #486255;
}

::-webkit-input-placeholder {
  font-family: var(--wrapper_font);
}

.button-center {
  text-align: center;
  margin-bottom: 20px;
}

.contact__heading__container {
  text-align: center;
  margin-bottom: 30px;
}
.about__underline__contact {
  margin-top: 7px;
  height: 4px;
  display: inline-block;
  width: 55px;
  border-radius: 2px;
  background-color: #fff;
}

#hvqst {
  font-size: 15px;
}
@media screen and (max-width: 1100px) {
  .form-holder {
    -webkit-clip-path: polygon(50% 8%, 100% 0, 100% 100%, 0 100%, 0 0);
            clip-path: polygon(50% 8%, 100% 0, 100% 100%, 0 100%, 0 0);
  }
}

@media screen and (max-width: 480px) {
  .form-holder {
    -webkit-clip-path: polygon(50% 8%, 100% 0, 100% 100%, 0 100%, 0 0);
            clip-path: polygon(50% 8%, 100% 0, 100% 100%, 0 100%, 0 0);
  }

  .contact__heading__container .contact-heading {
    margin-top: 50px;
    font-size: 1.7rem;
  }

  .about__underline__contact {
    width: 45px;
  }

  .container {
    padding: 20px;
  }

  #hvqst {
    font-size: 13px;
  }

  .input,
  .msg .area {
    padding: 10px;
  }

  .msg .area {
    height: 150px;
  }

  .btn {
    padding: 0.4em 1.5em;
    font-size: 0.9em;
  }
}

.footer {
  /* background-color: black; */
  background-color: #171010;
  padding: 20px;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  font-family: var(--font);
}

.social-media {
  width: 600px;
  max-width: 300px;

  display: flex;
  justify-content: space-around;
}

.social-media a {
  text-decoration: none;
}
.icon {
  width: 40px;
  height: 40px;
  display: grid;
  place-items: center;
  border: 1px solid #595260;
}
.icon:hover {
  display: grid;
  place-items: center;
  background: #962d2d;
  box-shadow: 0 2px 4px 0 rgb(0 0 0 / 50%);

  transition: 0.2s linear;
}

.icon i {
  font-size: 1.3rem;
  color: white;
}

.copyright {
  color: white;
  margin-top: 16px;
}

.copyright p {
  font-size: 1.11rem;
}

@media screen and (max-width: 480px) {
  .social-media {
    max-width: 230px;
  }

  .icon {
    width: 30px;
    height: 30px;
  }

  .icon i {
    font-size: 1rem;
  }

  .copyright p {
    font-size: 0.9em;
  }
}

.move__to__top__icon {
  background: #962d2d;
  height: 40px;
  width: 40px;
  position: fixed;
  cursor: pointer;
  bottom: 10px;
  right: 10px;
  z-index: 10;
  display: grid;
  place-items: center;
  border-radius: 50%;
  color: #fff;
  display: none;
}

@media screen and (max-width: 480px) {
  .move__to__top__icon {
    height: 32px;
    width: 32px;
    bottom: 10px;
    right: 10px;
  }
}

